import React, {FC, useEffect, useRef, useState} from 'react';
import {makeStyles} from "@mui/styles";
import {Typography} from "@mui/material";
import 'react-h5-audio-player/lib/styles.css';
import {callsSlice} from "../../../store/calls/calls.slice";
import {useDispatch} from "react-redux";
import {CallAudioType, CallInfoType, CallSttType, TagType,} from "../../../store/calls/calls.types";
import Plus from "../../../components/Buttons/Plus";
import Reboot from "../../../components/Buttons/Reboot";
import History from "../../../components/Buttons/History";
import Download from "../../../components/Buttons/Download";
import Back from "../../../components/Buttons/Back";
import {BlockBox} from "../../../components";
import DialogItem from "./DialogItem";
import {BaseTag} from "../../../components/Tag";
import AudioPlayer from "../../../components/AudioPlayer";

type CallBodyPropsType = {
  callInfo: CallInfoType,
  callAudio: CallAudioType | null,
  callStt: CallSttType | null,
  bundleIndex: number,
  expanded: boolean,
  fragments: TagType[]
};

const CallBody: FC<CallBodyPropsType> = React.memo(({
                                                      callInfo,
                                                      callAudio,
                                                      callStt,
                                                      fragments,
                                                      bundleIndex,
                                                      expanded
                                                    }) => {
  const useStyles = makeStyles(({
    callBodyWrapper: {
      position: 'sticky',
      top: '0',
    },
    cbDialogWrapper: {
      height: '765px',
      width: '100%',
      backgroundColor: '#EEF2F6'
    },
    cbDialogInner: {
      padding: '12.5px 12px'
    },
    cbDialogItems: {
      overflowY: 'auto',
      height: '765px',
      '&::-webkit-scrollbar': {
        width: '4px',
        backgroundColor: 'f1f1f1',
        outline: 'none',

      },
      '&::-webkit-scrollbar-thumb': {
        background: '#A3AEBE',
        height: '50px',
        borderRadius: '10px'
      },
      '&::-webkit-scrollbar-thumb:hover': {
        background: '#555'
      }
    },
    audioPlayerBox: {
      //   overflow: 'hidden',
      //   position: 'sticky',
      //   top: `120px`,
      //   zIndex: '101',
      //   height: '120px',
    },

    controlBlockButtonBox: {
      boxShadow: 'none !important',
      padding: '0 5px',
      marginLeft: '20px'
    },
    controlBlockButton: {
      border: 'none !important',
      transition: '0.4s !important',
      outline: 'none !important',
      height: '40px',
      fontSize: '14px !important',
      // @ts-ignore
      textTransform: 'none !important',
      color: '#738094 !important',
      backgroundColor: '#ffffff !important',
      '&.Mui-selected': {
        backgroundColor: '#D6D9DF !important',
        color: '#000 !important'
      }
    },
    isActive: {
      backgroundColor: '#CDD5DF',
      color: '#000'
    },
    typographyTitle: {
      color: '#738094 !important',
      fontWeight: '700 !important',
      minWidth: '110px !important',
    }
  }));
  const classes = useStyles();
  const dispatch = useDispatch();


  useEffect(() => {
    if (!expanded && callAudio) {
      dispatch(callsSlice.actions.removeAudio({id: callInfo.id, bundleIndex}));
    }
  }, [expanded]);

  // Ð¿Ð¾ÑÐ¸ÑÐ°ÑÑ Ð¿ÑÐ¾:
  // useCallback
  // useMemo
  // memo

  const currentTime = useRef(0);
  const callId = callInfo.id;

  const prevIndex = useRef<string[] | undefined>(undefined);
  const currentIndex = useRef<string[] | undefined>(undefined);

  useEffect(() => {
    return () => {
      currentTime.current = 0;
      prevIndex.current = undefined;
      currentIndex.current = undefined;
    }
  }, [])

  const getIndices = (array: Array<any>, necessaryElements: any) => {
    let indices: number[] = []
    for (let i = 0; i < necessaryElements.length; i++) {
      indices.push(array.indexOf(necessaryElements[i]));
    }
    return indices;
  }

  // ÐÐ¾Ð·Ð²ÑÐ°ÑÐ°ÐµÑ ÑÑÑÐ¾ÐºÑ Ñ Ð¸Ð½Ð´ÐµÐºÑÐ¾Ð¼ ÑÑÐ°Ð³Ð¼ÐµÐ½ÑÐ°/ÑÑÐ°Ð·Ñ Ð¸ ÑÐ»Ð¾Ð²Ð°.
  function findWordIndex(phrases: any[] | undefined, currentTime: number) {
    if (phrases) {
      const currentFragments = phrases.filter(phrase => phrase.begin <= currentTime && currentTime <= phrase.end);
      const currentFragmentsIndices = getIndices(phrases, currentFragments);

      if (currentFragmentsIndices.length < 1 || currentFragmentsIndices.some(index => index < 0)) {
        return undefined;
      }

      let indices = []
      for (let i = 0; i < currentFragments.length; i++) {
        const currentWords = currentFragments[i].words.filter((word: any) => word.begin <= currentTime && currentTime <= word.end);
        const currentWordsIndices = getIndices(currentFragments[i].words, currentWords);

        for (let j = 0; j < currentWords.length; j++) {
          indices.push(`${callId}-${currentFragmentsIndices[i]}-${currentWordsIndices[j]}`)
        }
      }
      if (indices.length < 1) {
        return undefined;
      }
      return indices;
    }
  } // return [1-4, 2-8]


  const onListen = (eventCurrentTime: any) => {
    let currentTimeLocal;
    if (!!eventCurrentTime.target) {
      currentTimeLocal = eventCurrentTime.target.currentTime * 1000;
    } else {
      currentTimeLocal = eventCurrentTime * 1000;
    }

    currentTime.current = currentTimeLocal;
    if (callStt) {
      const indices = findWordIndex(callStt.fragments, currentTimeLocal);

      if (indices && JSON.stringify(indices) !== JSON.stringify(prevIndex.current)) {

        let activeWord = null;
        for (let i = 0; i < indices.length; i++) {
          activeWord = document.getElementById(`${indices[i]}`);
          if (activeWord) {
            activeWord.classList.add(classes.isActive);
          }
        }

        if (prevIndex.current) {
          for (let i = 0; i < prevIndex.current.length; i++) {
            // ÑÐ±Ð¸ÑÐ°ÐµÐ¼ ÑÐ°ÑÐºÑÐ°ÑÐºÑ Ñ Ð¿ÑÐµÐ´ÑÐ´ÑÑÐµÐ³Ð¾
            const removeElement = document.getElementById(`${prevIndex.current[i]}`);
            if (removeElement) {
              removeElement.classList.remove(classes.isActive)
            }
          }
        }

        // Ð½Ð¾Ð²Ð¾ÐµÐ¿ÑÐµÐ´ÑÐ´ÑÑÐµÐµ = ÑÐµÐºÑÑÐµÐµ
        prevIndex.current = indices;

        // if (activeWord) {
        //   activeWord.scrollIntoView({
        //     behavior: "smooth",
        //   });
        // }

      }
      if (!indices) {
        // ÑÐ±Ð¸ÑÐ°ÐµÐ¼ ÑÐ°ÑÐºÑÐ°ÑÐºÑ Ñ Ð¿ÑÐµÐ´ÑÐ´ÑÑÐµÐ³Ð¾
        const removeElement = document.getElementById(`${prevIndex.current}`);
        if (removeElement) {
          removeElement.classList.remove(classes.isActive)
        }
      }
      currentIndex.current = indices;
    }
  };

  console.log(fragments);

  return (
    <div className={classes.callBodyWrapper}>
      <div className={classes.audioPlayerBox}>
        <AudioPlayer
          callAudio={callAudio}
          listenInterval={1}
          onListen={onListen}
        />
      </div>
      <div style={{display: 'flex'}}>
        <div className={classes.cbDialogWrapper}>
          <div className={classes.cbDialogInner}>
            {fragments.length > 0 &&
            <>
              <Typography style={{marginBottom: '10px'}} className={classes.typographyTitle}>
                Ð¢ÐµÐ³Ð¸:
              </Typography>
              <div style={{display: 'flex', marginBottom: '15px'}}>
                {fragments.map((fragment) => (
                  <BaseTag body={fragment.name}/>
                ))}
              </div>
            </>
            }
            <Typography style={{marginBottom: '10px'}} className={classes.typographyTitle}>
              Ð¢ÐµÐºÑÑÐ¾Ð²ÑÐ¹ Ð´Ð¸Ð°Ð»Ð¾Ð³:
            </Typography>
            <div className={classes.cbDialogItems}>
              {callStt ?
                <div>
                  {callStt.fragments.map((phrase, i) => (
                    <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center'}}>
                      <div style={{width: '85%'}}>
                        <DialogItem
                          fragment={phrase}
                          callId={callId}
                          fragmentIndex={i}
                        />
                      </div>
                      <div style={{textAlign: 'center', width: '15%'}}>
                        {fragments.map((fragment, j) => {
                          if (phrase.begin === fragment.fBegin && phrase.end === fragment.fEnd) {
                            return <div>
                              <BaseTag body={fragment.name}/>
                            </div>;
                          }
                          return null;
                        })}
                      </div>
                    </div>
                  ))}
                </div>
                : null}
            </div>
          </div>
        </div>
        <div style={{backgroundColor: 'fff', width: '370px', marginTop: '20px'}}>
          <div style={{display: 'flex', justifyContent: 'center'}}>
            <Back/>
            <Download/>
            <History/>
            <Reboot/>
            <Plus margin={'0'}/>
          </div>

          {/* Params block*/}
          <BlockBox width={'auto'} height={'auto'} padding={'24px'} margin={'24px 14px 24px 24px'}>
            <div style={{display: 'flex'}}>
              <Typography className={classes.typographyTitle}>Ð¡Ð¾ÑÑÑÐ´Ð½Ð¸Ðº:</Typography>
              <Typography>{callInfo.operatorPhone}</Typography>
            </div>
            <div style={{display: 'flex'}}>
              <Typography className={classes.typographyTitle}>ÐÐ»Ð¸ÐµÐ½Ñ:</Typography>
              <Typography>{callInfo.clientPhone}</Typography>
            </div>
            <div style={{display: 'flex'}}>
              <Typography className={classes.typographyTitle}>ÐÐ°ÑÐ° Ð¸ Ð²ÑÐµÐ¼Ñ:</Typography>
              <Typography>{callInfo.callTimeReadable}</Typography>
            </div>
          </BlockBox>

          {/* Check list block */}
          {/*  <BlockBox width={'auto'} height={'auto'} padding={'24px'} margin={'24px'}>*/}
          {/*    <Typography>Ð§ÐµÐº Ð»Ð¸ÑÑ</Typography>*/}
          {/*    <ContainedSelect*/}
          {/*      value={checkListValue}*/}
          {/*      options={checkListOptions}*/}
          {/*      onSelectChange={(event: any) => {*/}
          {/*        setCheckListValue(event)*/}
          {/*      }}*/}
          {/*      width="274px"*/}
          {/*    />*/}

          {/*    <ToggleButtonGroup*/}
          {/*      className={classes.controlBlockButtonBox}*/}
          {/*      value={alignment}*/}
          {/*      exclusive*/}
          {/*      onChange={handleChange}*/}
          {/*    >*/}
          {/*      <ToggleButton*/}
          {/*        disabled={"today" === alignment}*/}
          {/*        className={classes.controlBlockButton} value="today"*/}
          {/*        onClick={() => {*/}
          {/*        }}*/}
          {/*      >*/}
          {/*        1*/}
          {/*      </ToggleButton>*/}

          {/*      <ToggleButton disabled={"yesterday" === alignment}*/}
          {/*                    className={classes.controlBlockButton} value="yesterday"*/}
          {/*                    onClick={() => {*/}
          {/*                    }}*/}
          {/*      >*/}
          {/*        2*/}
          {/*      </ToggleButton>*/}

          {/*      <ToggleButton*/}
          {/*        className={classes.controlBlockButton} disabled={"week" === alignment} value="week"*/}
          {/*        onClick={() => {*/}
          {/*        }}*/}
          {/*      >*/}
          {/*        3*/}
          {/*      </ToggleButton>*/}

          {/*      <ToggleButton*/}
          {/*        className={classes.controlBlockButton} disabled={"month" === alignment} value="month"*/}
          {/*        onClick={() => {*/}
          {/*        }}*/}
          {/*      >*/}
          {/*        4*/}
          {/*      </ToggleButton>*/}

          {/*      <ToggleButton*/}
          {/*        className={classes.controlBlockButton} disabled={"year" === alignment} value="year"*/}
          {/*        onClick={() => {*/}
          {/*        }}*/}
          {/*      >*/}
          {/*        5*/}
          {/*      </ToggleButton>*/}
          {/*    </ToggleButtonGroup>*/}
          {/*  </BlockBox>*/}

          {/*  /!* Comments input *!/*/}
          {/*  <BlockBox width={'auto'} height={'auto'} padding={'24px'} margin={'24px'}>*/}
          {/*    Params block*/}
          {/*  </BlockBox>*/}

        </div>
      </div>
    </div>
  );
});


export default CallBody;