import React, {useEffect} from 'react';
import {Redirect, Route, Switch, Link, useHistory} from 'react-router-dom';
import {makeStyles} from '@mui/styles';
import {Button, Typography} from '@mui/material';
import {Header} from './components';
import {Auth, Calls, MarkupRules} from './pages';
import {useAppSelector} from "./hooks/redux";
import {authSlice} from "./store/auth/auth.slice";
import {useDispatch} from "react-redux";
import LoadCall from "./pages/LoadCall";
import Settings from "./pages/Settings";
import "rsuite/dist/rsuite.min.css";
import 'antd/dist/antd.css';


export const useStyles = makeStyles(({
  wrapper: {
    width: 'auto',
  },
  container: {
    maxWidth: '1610px',
    margin: '0 auto',
    padding: '0 10px'
  }
}));

const App = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const isAuth = useAppSelector(state => state.auth.isAuth);
  useEffect(() => {
    const {token} = JSON.parse(localStorage.getItem('token') || '{}');
    if (token) {
      dispatch(authSlice.actions.setAuth(true))
    }
  }, []);


  const history = useHistory()

  useEffect(() => {
    history.listen((location) => {
      localStorage.setItem('path', JSON.stringify({
        path: location.pathname
      }));
    })
  }, [history]);

  useEffect(() => {
    const {path} = JSON.parse(localStorage.getItem('path') || '{}')
    if (path) {
      history.push(path);
    }
  }, [])

  const markupRulesPages = 'dictionaries' || 'tags' || 'checklists'
  return (
    <div className={classes.wrapper}>
      <Switch>

        {/* ÐÐ²ÑÐ¾ÑÐ¸Ð·Ð°ÑÐ¸Ñ */}
        <Route exact path="/auth">
          <Auth/>
        </Route>
        {!isAuth && <Redirect to="/auth"/>}

        {/* ÐÐ²Ð¾Ð½ÐºÐ¸ */}
        <Route path="/calls">
          <Header/>
          <div className={classes.container}>
            <Calls/>
          </div>
        </Route>

        {/* ÐÑÑÑÐµÑÑ */}
        <Route path="/reports">
          <Header/>
          <div className={classes.container}>
            <Typography style={{textAlign: 'center', color: 'rgba(0, 0, 0, 0.2)'}} variant="h3">reports</Typography>
          </div>
        </Route>

        {/* ÐÐ°Ð³ÑÑÐ·Ð¸ÑÑ Ð·Ð²Ð¾Ð½Ð¾Ðº */}
        <Route path="/markuprules">
          <MarkupRules/>
        </Route>

        {/* ÐÐ°Ð³ÑÑÐ·Ð¸ÑÑ Ð·Ð²Ð¾Ð½Ð¾Ðº */}
        <Route path="/upload">
          <Header/>
          <div className={classes.container}>
            <LoadCall/>
          </div>
        </Route>


        {/* ÐÐ¿Ð¾Ð²ÐµÑÐµÐ½Ð¸Ðµ */}
        <Route path="/alert">
          <Header/>
          <div className={classes.container}>
            <Typography style={{textAlign: 'center', color: 'rgba(0, 0, 0, 0.2)'}} variant="h3">alert</Typography>
          </div>
        </Route>

        {/* ÐÐ°ÑÑÑÐ¾Ð¹ÐºÐ¸ */}
        <Route path="/settings">
          <Header/>
          <div className={classes.container}>
            <Settings/>
          </div>
        </Route>

        <Route exact path="/">
          <Redirect to="calls"/>
        </Route>

        <Route exact path="*">
          <Button variant="outlined"><Link to="/">ÐÐ° Ð³Ð»Ð°Ð²Ð½ÑÑ ÑÑÑÐ°Ð½Ð¸ÑÑ.</Link></Button>
          <h1>404</h1>
        </Route>

      </Switch>
    </div>
  );
};

export default App
